import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Panel, Div, Button } from '@vkontakte/vkui';
import * as vkSelectors from '../store/vk/reducer';

import MainHeader from './MainHeader';
import Bar from './Bar';

class Passp extends Component {

    openMain = () => {
        this.props.router.navigate('main');
    }

    openVet = () => {
        this.props.router.navigate('vet');
    }

    openCow = () => {
        this.props.router.navigate('cow');
    }

    openMap = () => {
        this.props.router.navigate('map');
    }

    render() {
        return (
            <Panel id={this.props.id}>
                <MainHeader openMain={this.openMain} />
                <Div style={{
                    height: 'calc(100vh - 85px)',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center'
                }}>
                    <Div style={{height: 250, display: 'flex', flexDirection: 'column', justifyContent: 'space-between'}}>
                        <Div>
                            <Button size="xl" component="a" onClick={this.openVet}>Ветпаспорт</Button>
                        </Div>
                        <Div>
                            <Button size="xl" component="a" onClick={this.openCow}>Ветеринар</Button>
                        </Div>
                        <Div>
                            <Button size="xl" component="a" onClick={this.openMap}>Поиск клиники</Button>
                        </Div>
                    </Div>
                </Div>
                <Bar openChat={() => {}} />
            </Panel>
        );
    }
}

function mapStateToProps(state) {
    return {
        notificationStatus: vkSelectors.getNotificationStatus(state),
    };
}

export default connect(mapStateToProps)(Passp);
