import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Panel, View, PanelHeader, PanelHeaderBack, PanelHeaderButton, PanelHeaderContent, Avatar, Epic, Input, Tabbar, Div } from '@vkontakte/vkui';
import Icon24PhoneOutline from '@vkontakte/icons/dist/24/phone_outline';
import Icon24Attach from '@vkontakte/icons/dist/24/attach';
import Icon28Voice from '@vkontakte/icons/dist/28/voice';
import Icon44SmileOutline from '@vkontakte/icons/dist/44/smile_outline';

import MainHeader from './MainHeader';

import userImage from '../image/user.png';

class Chat extends Component {

    openProfile = () => {
        this.props.router.navigate('profile');
    }

    render() {
        return (
            <Panel id={this.props.id}>
                <MainHeader openMain={() => {}} />
                <View activePanel="brand" className="custoBg__">
                    <Panel id="brand">
                        <PanelHeader
                            left={<PanelHeaderBack onClick={this.openProfile} style={{color: '#3F8AE0'}} />}
                            right={<button className="customButton__"><Icon24PhoneOutline style={{color: '#3F8AE0'}} /></button>}
                        >
                            <PanelHeaderContent
                                status="Online"
                                before={<Avatar size={36} src={userImage} />}
                            >
                                Элизавета Корчагина
                            </PanelHeaderContent>
                        </PanelHeader>
                        <Div style={{
                            height: 'calc(100vh - 186px)',
                            display: 'flex',
                            flexDirection: 'column',
                            backgroundColor: '#E5E5E5',
                            overflowY: 'scroll'
                        }}>
                            <Div style={{display: 'flex', alignItems: 'bottom'}}>
                                <Avatar size={36} src={userImage} className="customAvatar___" />
                                <Div style={{backgroundColor: '#EBEDF0', padding: '8px 12px', borderRadius: '18px'}}><span className="customText___">Добрый день,<br />чем я могу вам помочь?</span></Div>
                            </Div>
                            <Div style={{display: 'flex', alignItems: 'bottom', justifyContent: 'flex-end'}}>
                                <Div style={{backgroundColor: '#CCE4FF', padding: '8px 12px', borderRadius: '18px'}}>
                                    <span className="customText___">
                                    Здравствуйте, у моей кошки<br />
                                    слезотечение, появление<br />
                                    гноя, склеивание век,<br />
                                    светобоязнь, боль в районе<br />
                                    глазного яблока
                                    </span></Div>
                            </Div>
                            <Div style={{display: 'flex', alignItems: 'bottom'}}>
                                <Avatar size={36} src={userImage} className="customAvatar___" />
                                <Div style={{backgroundColor: '#EBEDF0', padding: '8px 12px', borderRadius: '18px'}}>
                                    <span className="customText___">
                                        Это похоже на конъюнктивит<br />
                                        рекомендуем промывать глаза<br />
                                        раствором марганцовки, ромашкой,<br />
                                        приобрести глазную мазь<br />
                                        Тетрациклиновую. Когда появится<br />
                                        возможность обратиться в клинике
                                    </span>
                                </Div>
                            </Div>
                            <Div style={{display: 'flex', alignItems: 'bottom'}}>
                                <Avatar size={36} src={userImage} className="customAvatar___" />
                                <Div style={{backgroundColor: '#EBEDF0', padding: '8px 12px', borderRadius: '18px'}}>
                                    <span className="customText___">
                                        Это похоже на конъюнктивит<br />
                                        рекомендуем промывать глаза<br />
                                        раствором марганцовки, ромашкой,<br />
                                        приобрести глазную мазь<br />
                                        Тетрациклиновую. Когда появится<br />
                                        возможность обратиться в клинике
                                    </span>
                                </Div>
                            </Div>
                            <Div style={{display: 'flex', alignItems: 'bottom'}}>
                                <Avatar size={36} src={userImage} className="customAvatar___" />
                                <Div style={{backgroundColor: '#EBEDF0', padding: '8px 12px', borderRadius: '18px'}}>
                                    <span className="customText___">
                                        Это похоже на конъюнктивит<br />
                                        рекомендуем промывать глаза<br />
                                        раствором марганцовки, ромашкой,<br />
                                        приобрести глазную мазь<br />
                                        Тетрациклиновую. Когда появится<br />
                                        возможность обратиться в клинике
                                    </span>
                                </Div>
                            </Div>
                            <Div style={{display: 'flex', alignItems: 'bottom'}}>
                                <Avatar size={36} src={userImage} className="customAvatar___" />
                                <Div style={{backgroundColor: '#EBEDF0', padding: '8px 12px', borderRadius: '18px'}}>
                                    <span className="customText___">
                                        Это похоже на конъюнктивит<br />
                                        рекомендуем промывать глаза<br />
                                        раствором марганцовки, ромашкой,<br />
                                        приобрести глазную мазь<br />
                                        Тетрациклиновую. Когда появится<br />
                                        возможность обратиться в клинике
                                    </span>
                                </Div>
                            </Div>
                        </Div>
                    </Panel>
                </View>
                <Epic className="EpicCustom__" tabbar={
                    <Tabbar style={{width: '100%', position: 'relative'}}>
                        <Icon24Attach height={24} style={{ color: '#99A2AD', position: 'absolute', zIndex: 10, left: '10px', top: '50%', transform: 'translate(0, -50%)'}} />
                        <Input style={{width: '100%', height: '100%', borderRadius: 0, border: 'none', paddingLeft: '50px', paddingRight: '100px'}} placeholder="Сообщение" />
                        <Icon28Voice height={24} style={{ color: '#99A2AD', position: 'absolute', zIndex: 10, right: '10px', top: '50%', transform: 'translate(0, -50%)'}} />
                        <Icon44SmileOutline height={24} style={{ color: '#99A2AD', position: 'absolute', zIndex: 10, right: '36px', top: '50%', transform: 'translate(0, -50%)'}} />
                    </Tabbar>
                }></Epic>
            </Panel>
        );
    }
}

function mapStateToProps(state) {
    return {};
}

export default connect(mapStateToProps)(Chat);
