import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Panel, Avatar, Group, Button, Div, Input, Header } from '@vkontakte/vkui';

import Icon28WriteOutline from '@vkontakte/icons/dist/28/write_outline';

import '../css/RichCell.css';
import * as vkSelectors from '../store/vk/reducer';

import MainHeader from './MainHeader';
import Bar from './Bar';

import st1 from '../image/st1.png';
import st2 from '../image/st2.png';
import ser from '../image/serch.png';
import ic2 from '../image/ic2.png';

class Cow extends Component {

   
    openMain = () => {
        this.props.router.navigate('main');
    }

    openChat = () => {
        this.props.router.navigate('chat');
    }

    render() {
        return (
            <Panel id={this.props.id}>
                <MainHeader openMain={this.openMain} />
                <Group style={{height: 'calc(100vh - 113px)', overflowY: 'scroll'}}>
                    
                    
                    
                    <Div style={{position: 'relative'}}>
                        <img src={ser} style={{top: '50%', zIndex: 6, transform: 'translate(0, -50%)', position: 'absolute', left: 30}} />
                        <Input style={{paddingLeft: 40}} placeholder="Поиск" />
                        <img src={ic2} style={{top: '50%', zIndex: 6, transform: 'translate(0, -50%)', position: 'absolute', right: 30}} />
                    </Div>
                    
                    <Div style={{display: 'flex', justifyContent: 'space-between'}}>
                        <Div>Ветеринары</Div> <Div><span style={{color: '#3F8AE0'}}>Показать все</span></Div>
                    </Div>
                    
                    
                    <div className="Tappable_custom">
                        <Avatar size={72} src="https://sun9-16.userapi.com/c857724/v857724589/9c7ad/01M-8UlcNoo.jpg?ava=1" />
                        <div className="RichCell__in">
                            <div className="RichCell__top">
                        
                                <div className="RichCell__content">
                                    <div className="RichCell__children child___customTitle" style={{position: 'relative'}}>Тарас Иванов
                                    
                                    <div style={{display: 'flex', position: 'absolute', top: 3, right: 0}}>
                                            <img src={st1} />
                                            <img src={st1} />
                                            <img src={st1} />
                                            <img src={st2} />
                                            <img src={st2} />
                                            <img src={st2} />
                                        </div>
                                    </div>
                                </div>
                                <div className="RichCell__caption" style={{marginLeft: 0}}>Ветклиника ЗооШтат г.Москва,<br /> улица Большие Каменщики, дом 2</div>
                            

                                <div className="RichCell__bottom">
                                    <div className="RichCell__actions">
                                        <Button className="customButtonCow">Принять</Button>
                                        <Button className="customButtonCowGray">Отклонить</Button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    
                    </div>

                    <div className="Tappable_custom">
                        <Avatar size={72} src="https://sun9-34.userapi.com/c857132/v857132690/49628/r4wBoWw0mJI.jpg?ava=1" />
                        <div className="RichCell__in">
                            <div className="RichCell__top">
                        
                                <div className="RichCell__content">
                                    <div className="RichCell__children child___customTitle" style={{position: 'relative'}}>Илья Гришин
                                    
                                    <div style={{display: 'flex', position: 'absolute', top: 3, right: 0}}>
                                            <img src={st1} />
                                            <img src={st1} />
                                            <img src={st1} />
                                            <img src={st1} />
                                            <img src={st1} />
                                            <img src={st1} />
                                        </div>
                                    </div>
                                </div>
                                <div className="RichCell__caption" style={{marginLeft: 0}}>Ветклиника Медвет г.Москва,<br /> проспект Ленинский, дом 123</div>
                            

                                <div className="RichCell__bottom">
                                    <div className="RichCell__actions">
                                        <Button className="customButtonCow">Принять</Button>
                                        <Button className="customButtonCowGray">Отклонить</Button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    
                    </div>

                    <div className="Tappable_custom">
                        <Avatar size={72} src="https://sun9-60.userapi.com/c851416/v851416327/be840/bnUHAblZoBY.jpg?ava=1" />
                        <div className="RichCell__in">
                            <div className="RichCell__top">
                        
                                <div className="RichCell__content">
                                    <div className="RichCell__children child___customTitle" style={{position: 'relative'}}>Алексей Иванов
                                    
                                    <div style={{display: 'flex', position: 'absolute', top: 3, right: 0}}>
                                            <img src={st1} />
                                            <img src={st1} />
                                            <img src={st1} />
                                            <img src={st1} />
                                            <img src={st1} />
                                            <img src={st2} />
                                        </div>
                                    </div>
                                </div>
                                <div className="RichCell__caption" style={{marginLeft: 0}}>Айболит Плюс г.Москва,<br/> улица Кленовый бульвар, 28</div>
                            

                                <div className="RichCell__bottom">
                                    <div className="RichCell__actions">
                                        <Button className="customButtonCow">Принять</Button>
                                        <Button className="customButtonCowGray">Отклонить</Button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    
                    </div>

                    
                </Group>

                <Bar openChat={this.openChat} />
            </Panel>
        );
    }
}

function mapStateToProps(state) {
    return {
        notificationStatus: vkSelectors.getNotificationStatus(state)
    };
}

export default connect(mapStateToProps)(Cow);
