import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Panel, Div, Input } from '@vkontakte/vkui';
import * as vkSelectors from '../store/vk/reducer';

import MainHeader from './MainHeader';
import Bar from './Bar';

import Search from '../image/serch.png';
import Icns from '../image/icns.png';
import Ic1 from '../image/ic1.png';
import Ic2 from '../image/ic2.png';
import Ic3 from '../image/ic3.png';

class Vet extends Component {

    openMain = () => {
        this.props.router.navigate('main');
    }

    render() {
        return (
            <Panel id={this.props.id}>
                <MainHeader openMain={this.openMain} />
                <Div style={{padding:0, backgroundColor: '#EBEDF0', display: 'flex', justifyContent: 'space-between'}}>
                    <div style={{display: 'flex'}}>
                        <Div><img src={Search} /></Div>
                        <Input style={{paddingLeft: 5}} className="customInput____" placeholder="Цефален" />
                    </div>
                    <div style={{display: 'flex'}}>
                        <Div><img height={14} src={Icns} /></Div>
                        <Div><img height={14} src={Ic1} /></Div>
                        <Div><img height={14} src={Ic2} /></Div>
                        <Div><img height={14} src={Ic3} /></Div>
                    </div>
                </Div>
                <Div>
                    <Div>
                        <h1 style={{fontSize: 20, margin: 0}}>05.06.2019 Мильбемакс, 1табл</h1>
                    </Div>
                    <Div>
                        <h1 style={{fontSize: 20, margin: 0}}>22.06.2019 Вакцинация “Нобивак”</h1>
                    </Div>
                    <Div>
                        <h1 style={{fontSize: 20, margin: 0, color: 'red'}}>17.09.2019 Цефален 1 табл.</h1>
                    </Div>
                    <Div>
                        <h1 style={{fontSize: 20, margin: 0}}>06.06.2020 Мибемакс, 1 табл.</h1>
                    </Div>
                    <Div>
                        <h1 style={{fontSize: 20, margin: 0}}>20.06.2020 Вакцинация “Нобивак”</h1>
                    </Div>
                </Div>
                <Bar openChat={() => {}} />
            </Panel>
        );
    }
}

function mapStateToProps(state) {
    return {
        notificationStatus: vkSelectors.getNotificationStatus(state),
    };
}

export default connect(mapStateToProps)(Vet);
